<template>
    <v-app>
        <v-row>
            <v-col>
                <v-sheet height="64">
                    <v-toolbar flat>
                        <v-toolbar-title class="ml-5">
                            選擇日期
                        </v-toolbar-title>
                        <v-btn text absolute right @click="$router.push('/')">&#10005;</v-btn>
                    </v-toolbar>
                </v-sheet>
                <v-sheet height="500">
                    <v-calendar :now="today" :value="today" color="primary">
                        <template v-slot:day="{ past, date }">
                            <v-row class="fill-height">
                                <template v-if="past && tracked[date]">
                                    <v-sheet
                                        v-for="(percent, i) in tracked[date]"
                                        :key="i"
                                        :title="category[i]"
                                        :color="colors[i]"
                                        :width="`${percent}%`"
                                        height="100%"
                                        tile
                                    ></v-sheet>
                                </template>
                            </v-row>
                        </template>
                    </v-calendar>
                </v-sheet>
                <v-row class="d-flex justify-center mt-6">
                    <v-btn elevation="7" class="secondary" @click="$router.push('/')">選擇</v-btn>
                </v-row>
            </v-col>
        </v-row>
    </v-app>
</template>
<script>
import { Component, Vue } from 'vue-property-decorator';

@Component({
    data: () => ({
        today: '2021-04-09',
        tracked: {},
        colors: ['#1867c0', '#fb8c00', '#000000'],
        category: ['Development', 'Meetings', 'Slacking'],
    }),
})
export default class Home extends Vue {}
</script>
