<template>
    <v-app>
        <v-row class="fill-height">
            <v-col>
                <v-sheet height="64">
                    <v-toolbar flat>
                        <v-toolbar-title class="ml-5" v-if="type == 'day'">
                            <v-btn fab text small color="grey darken-2" @click="prev">
                                <v-icon small class="rounded-circle grey white--text">
                                    mdi-chevron-left
                                </v-icon>
                            </v-btn>
                            {{ $refs.calendar.title }}
                            <v-btn fab text small color="grey darken-2" @click="next">
                                <v-icon small class="rounded-circle grey white--text">
                                    mdi-chevron-right
                                </v-icon>
                            </v-btn>
                        </v-toolbar-title>
                        <v-toolbar-title class="ml-5" v-else>
                            <!-- v-if="$refs.calendar"{{ $refs.calendar.title }} -->
                            預約行事曆
                        </v-toolbar-title>
                        <v-spacer></v-spacer>
                        <v-menu bottom right>
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn class="mr-4 rounded-pill primary" v-bind="attrs" v-on="on">
                                    <span class="secondary--text">{{ typeToLabel[type] }}</span>
                                    <v-icon right color="secondary">
                                        mdi-menu-down
                                    </v-icon>
                                </v-btn>
                            </template>
                            <v-list class="primary">
                                <v-list-item @click="type = 'day'">
                                    <v-list-item-title class="secondary--text">日檢視</v-list-item-title>
                                </v-list-item>
                                <v-list-item @click="type = 'week'">
                                    <v-list-item-title class="secondary--text">周檢視</v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                        <v-menu bottom right>
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn class="rounded-pill primary" v-bind="attrs" v-on="on">
                                    <span class="secondary--text">莉</span>
                                    <v-icon right color="secondary">
                                        mdi-menu-down
                                    </v-icon>
                                </v-btn>
                            </template>
                            <v-list class="primary">
                                <v-list-item>
                                    <v-list-item-title class="secondary--text">莉</v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                    </v-toolbar>
                </v-sheet>
                <v-sheet height="600">
                    <v-calendar
                        ref="calendar"
                        v-model="focus"
                        color="primary"
                        :events="events"
                        :event-color="getEventColor"
                        :type="type"
                        @click:event="showEvent"
                        @click:more="viewDay"
                        @click:date="viewDay"
                        @change="updateRange"
                    ></v-calendar>
                    <v-menu
                        v-model="selectedOpen"
                        :close-on-content-click="false"
                        :activator="selectedElement"
                        offset-x
                    >
                        <v-card color="purple bg-purple lighten-4" min-width="350px" flat>
                            <v-toolbar :color="selectedEvent.color" dark>
                                <v-btn icon>
                                    <v-icon>mdi-pencil</v-icon>
                                </v-btn>
                                <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                                <v-spacer></v-spacer>
                                <v-btn icon>
                                    <v-icon>mdi-heart</v-icon>
                                </v-btn>
                                <v-btn icon>
                                    <v-icon>mdi-dots-vertical</v-icon>
                                </v-btn>
                            </v-toolbar>
                            <v-card-text>
                                <span v-html="selectedEvent.details"></span>
                            </v-card-text>
                            <v-card-actions>
                                <v-btn text color="secondary" @click="selectedOpen = false">
                                    Cancel
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-menu>
                </v-sheet>
                <v-row class="d-flex justify-center mt-6">
                    <v-btn elevation="7" class="secondary" @click="$router.push('/day')">+新增預約</v-btn>
                </v-row>
            </v-col>
        </v-row>
    </v-app>
</template>

<script>
import { Component, Vue } from 'vue-property-decorator';

@Component({
    data: () => ({
        focus: '',
        type: 'week',
        typeToLabel: {
            week: '周檢視',
            day: '日檢視',
        },
        selectedEvent: {},
        selectedElement: null,
        selectedOpen: false,
        events: [],
        colors: ['grey', 'grey', 'grey', 'grey'],
        names: ['美甲保養', '美甲保養', '美甲保養', '美甲保養'],
    }),
    mounted() {
        this.$refs.calendar.checkChange();
    },
    methods: {
        viewDay({ date }) {
            this.focus = date;
            this.type = 'day';
        },
        getEventColor(event) {
            return event.color;
        },
        setToday() {
            this.focus = '';
        },
        prev() {
            this.$refs.calendar.prev();
        },
        next() {
            this.$refs.calendar.next();
        },
        showEvent({ nativeEvent, event }) {
            const open = () => {
                this.selectedEvent = event;
                this.selectedElement = nativeEvent.target;
                setTimeout(() => {
                    this.selectedOpen = true;
                }, 10);
            };

            if (this.selectedOpen) {
                this.selectedOpen = false;
                setTimeout(open, 10);
            } else {
                open();
            }

            nativeEvent.stopPropagation();
        },
        updateRange({ start, end }) {
            const events = [];

            const min = new Date(`${start.date}T00:00:00`);
            const max = new Date(`${end.date}T23:59:59`);
            const days = (max.getTime() - min.getTime()) / 86400000;
            const eventCount = this.rnd(days, days + 20);

            for (let i = 0; i < eventCount; i++) {
                const allDay = this.rnd(0, 3) === 0;
                const firstTimestamp = this.rnd(min.getTime(), max.getTime());
                const first = new Date(firstTimestamp - (firstTimestamp % 900000));
                const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000;
                const second = new Date(first.getTime() + secondTimestamp);

                events.push({
                    name: this.names[this.rnd(0, this.names.length - 1)],
                    start: first,
                    end: second,
                    color: this.colors[this.rnd(0, this.colors.length - 1)],
                    timed: !allDay,
                });
            }

            this.events = events;
        },
        rnd(a, b) {
            return Math.floor((b - a + 1) * Math.random()) + a;
        },
    },
})
export default class Home extends Vue {}
</script>
